import Platform from "./platform";
import platform from "../../img/platform-2.png";
import platformSmallTall from "../../img/platformSmallTall-02.png";
import { createImage } from "../utils";

let platformImage = createImage(platform);
let platformSmallTallImage = createImage(platformSmallTall);

class PlatformItems {
  constructor() {
    this.platforms = [];
    this.scrollPoints = [];
  }
  generateTerrain(playerPos, s_point) {
    this.scrollPoints.push(s_point);

    const randomLocation = Math.round(Math.random() * 1500) + playerPos + 100;
    if (Math.round(Math.random() * 10) <= 3) {
      this.platforms.unshift(
        new Platform({
          x: randomLocation + platformSmallTallImage.width + 1200,
          y: Math.round(Math.random() * 300),
          image: platformSmallTallImage,
        })
      );
    }
    if (Math.round(Math.random() * 10) <= 5) {
      this.platforms.unshift(
        new Platform({
          x: randomLocation - platformSmallTallImage.width * 2 + 1200,
          y: Math.round(Math.random() * 500) + 300,
          image: platformSmallTallImage,
        })
      );
    }

    if (Math.round(Math.random() * 10) <= 5) {
      this.platforms.unshift(
        new Platform({
          x: randomLocation - platformSmallTallImage.width * 2 + 1200,
          y: Math.round(Math.random() * 300) + 200,
          image: platformSmallTallImage,
        })
      );
      this.platforms.unshift(
        new Platform({
          x: randomLocation - platformSmallTallImage.width + 1200,
          y: 250,
          image: platformSmallTallImage,
        })
      );
    }

    this.platforms.push(
      new Platform({ x: randomLocation + 1200, y: 470, image: platformImage })
    );
  }
  platformGenerator() {
    return [
      new Platform({
        x: platformImage.width * 4 + 300 - platformSmallTallImage.width * 2,
        y: 300,
        image: platformSmallTallImage,
      }),
      new Platform({
        x: platformImage.width * 4 + 300 - platformSmallTallImage.width,
        y: 250,
        image: platformSmallTallImage,
      }),
      new Platform({ x: -1, y: 470, image: platformImage }),
      new Platform({
        x: platformImage.width - 3,
        y: 470,
        image: platformImage,
      }),
      new Platform({
        x: platformImage.width * 2 + 100,
        y: 470,
        image: platformImage,
      }),
      new Platform({
        x: platformImage.width * 3 + 300,
        y: 470,
        image: platformImage,
      }),
      new Platform({
        x: platformImage.width * 4 + 650,
        y: 470,
        image: platformImage,
      }),
    ];
  }
}

export default PlatformItems;
