import { canvas, c } from "./canvas_core";
import bunny from "../img/bunny/Idle (34x44).png";
import bunnyRun from "../img/bunny/Run (34x44).png";
import bunnyJump from "../img/bunny/Jump.png";
import { createImage, gravity } from "./utils";
import bunnyConfig from "./characters/bunny";
import pigConfig from "./characters/pig";
import batConfig from "./characters/bat";
import sonicConfig from "./characters/sonic";
import marioConfig from "./characters/mario";
import michaelJacksonConfig from "./characters/michaeljackson";

export class Player {
  constructor() {
    this.playerList = [sonicConfig];
    this.currentCharacter = sonicConfig;
    this.position = {
      x: 100,
      y: 100,
    };
    this.velocity = {
      x: 0,
      y: 0,
    };
    this.width = this.currentCharacter.width;
    this.height = this.currentCharacter.height;

    this.originalSpeed = 5;
    this.nitroSpeed = 15;

    this.speed = this.originalSpeed;
    this.isJumping = false;
    this.isRunning = false;
    this.isStanding = true;
    this.facingDirection = "right";
    this.jump = false;
    this.jumpHeight = 14;
    this.frames = 0;
    this.image = createImage(bunny);
    this.startFrame = 0;
    this.sprites = this.currentCharacter.sprites;

    this.currentSprite = this.sprites.stand.right;
    this.currentPlayer = 0;
  }
  mirrorImage(horizontal = false, vertical = false) {
    c.save(); // save the current canvas state
    c.setTransform(
      horizontal ? -1 : 1,
      0, // set the direction of x axis
      0,
      vertical ? -1 : 1, // set the direction of y axis
      this.position.x + (horizontal ? this.width : 0), // set the x origin
      this.position.y + (vertical ? this.height : 0) // set the y origin
    );
    c.drawImage(
      this.currentSprite.image,
      this.currentCharacter.imageWidth * this.frames,
      0,
      this.currentCharacter.imageWidth,
      this.currentCharacter.imageHeight,
      0,
      0,
      this.width,
      this.height
    );
    c.restore();
  }
  drawLeft() {
    c.drawImage(
      this.currentSprite.image,
      this.currentCharacter.imageWidth * this.frames,
      0,
      this.currentCharacter.imageWidth,
      this.currentCharacter.imageHeight,
      this.position.x,
      this.position.y,
      this.width,
      this.height
    );
  }
  drawRight() {
    this.mirrorImage(true, false);
  }
  draw() {
    this.facingDirection == "right" ? this.drawRight() : this.drawLeft();
  }
  changePlayer() {
    if (this.currentPlayer < this.playerList.length - 1) {
      this.currentPlayer++;
    } else {
      this.currentPlayer = 0;
    }

    this.currentCharacter = this.playerList[this.currentPlayer];
    this.sprites = this.currentCharacter.sprites;
    this.width = this.currentCharacter.width;
    this.height = this.currentCharacter.height;
    this.frames = 0;
    this.startFrame = 0;
  }
  update() {
    // if (this.speed === this.nitroSpeed) {
    //   this.currentCharacter = bunnyConfig;
    //   this.sprites = this.currentCharacter.sprites;
    //   this.width = this.currentCharacter.width;
    //   this.height = this.currentCharacter.height;
    // } else {
    //   this.currentCharacter = pigConfig;
    //   this.sprites = this.currentCharacter.sprites;
    //   this.width = this.currentCharacter.width;
    //   this.height = this.currentCharacter.height;
    // }

    this.startFrame == 0 && this.frames++;
    this.startFrame++;
    this.startFrame > this.currentSprite.frameRate &&
      (this.startFrame = !this.startFrame);
    this.frames > this.currentSprite.frameCount && (this.frames = 0);
    this.draw();
    this.position.y += this.velocity.y;
    this.position.x += this.velocity.x;

    if (this.height + this.position.y + this.velocity.y <= canvas.height) {
      this.velocity.y += gravity;
    }
  }
}
