import { canvas, c } from "./canvas_core";
import { Player } from "./player";
import Platform from "./platform";
import GenericObject from "./generic_object";
import { createImage, gravity } from "./utils";
import platform from "../img/platform-2.png";
import platformSmallTall from "../img/platformSmallTall-02.png";

import background from "../img/background2.jpg";
import hills from "../img/hills-01.png";
import PlatformItems from "./platform-assets/platform_generator";
import Menu from "./menu";
import bunnyConfig from "./characters/bunny";
import { Enemy } from "./enemy";
import batConfig from "./characters/bat";
import EnemyGenerator from "./enemy_generator";
const platform_items = new PlatformItems();
const generate_enemies = new EnemyGenerator();
let player = null;
let pig = null;
let rhino = null;
let bunny = null;
let genericObjects = [];

let backPoints = [];
let enemies = [];

let scrollOffSet = 0;
function init() {
  Menu().clear();
  enemies = [];
  player = new Player();
  pig = new Enemy("bat", 2200);
  rhino = new Enemy("rhino", 3200);
  bunny = new Enemy("bunny", 1200);

  enemies.push(pig);
  enemies.push(rhino);
  enemies.push(bunny);

  function createImage(imageSrc) {
    const image2 = new Image();
    image2.src = imageSrc;

    return image2;
  }

  platform_items.platforms = platform_items.platformGenerator();
  675;
  genericObjects = [
    new GenericObject({ x: -1127, y: -1, image: createImage(background) }),

    new GenericObject({ x: -1, y: -1, image: createImage(background) }),
    new GenericObject({ x: 1127, y: -1, image: createImage(background) }),

    new GenericObject({ x: -1, y: -1, image: createImage(hills) }),
  ];
  platform_items.scrollPoints = [];

  generate_enemies.scrollPoints = [];
  backPoints = [];

  scrollOffSet = 0;
}

const keys = {
  right: {
    pressed: false,
  },
  left: {
    pressed: false,
  },
  space: {
    pressed: false,
  },
};

init();
function animate() {
  requestAnimationFrame(animate);

  c.fillStyle = "white";
  c.fillRect(0, 0, canvas.width, canvas.height);

  genericObjects.forEach((genericObject) => {
    genericObject.draw();
  });
  platform_items.platforms.forEach((platform) => {
    platform.draw();
  });

  c.font = "30px Arial";
  c.fillText(Math.round(scrollOffSet / 100), 10, 50);

  if (keys.right.pressed && player.position.x <= 400) {
    player.velocity.x = player.currentCharacter.speed;
    // keys.space.pressed && (player.velocity.x += 5);
  } else if (keys.left.pressed && player.position.x >= 100) {
    player.velocity.x = -player.currentCharacter.speed;
  } else {
    player.velocity.x = 0;

    if (keys.right.pressed) {
      scrollOffSet += player.currentCharacter.speed;
      genericObjects.forEach((genericObject) => {
        genericObject.position.x -= player.currentCharacter.speed * 0.66;
      });
      enemies.forEach((enemy) => {
        enemy.startPoint < scrollOffSet && (enemy.isActive = true);
        if (enemy.isActive) {
          enemy.position.x -= player.currentCharacter.speed;
        }
      });

      platform_items.platforms.forEach((platform) => {
        platform.position.x -= player.currentCharacter.speed;
        // keys.space.pressed && (platform.position.x -= incrementSpace);
      });
    } else if (keys.left.pressed) {
      enemies.forEach((enemy) => {
        if (enemy.isActive) {
          enemy.position.x += player.currentCharacter.speed;
        }
      });

      scrollOffSet -= player.currentCharacter.speed;
      genericObjects.forEach((genericObject) => {
        genericObject.position.x += player.currentCharacter.speed * 0.66;
      });
      platform_items.platforms.forEach((platform) => {
        platform.position.x += player.currentCharacter.speed;
      });
    }
  }
  let pointBack = scrollOffSet - (scrollOffSet % (canvas.width - 349));

  if (!backPoints.includes(pointBack)) {
    console.log("pointBack", pointBack);

    backPoints.push(pointBack);
    genericObjects.unshift(
      new GenericObject({
        x: pointBack,
        y: -1,
        image: createImage(background),
      })
    );
  }

  let point = scrollOffSet - (scrollOffSet % (canvas.width - 349));

  if (!platform_items.scrollPoints.includes(point) && scrollOffSet > 0) {
    platform_items.generateTerrain(player.position.x, point);
  }

  // if (!generate_enemies.scrollPoints.includes(point) && scrollOffSet > 0) {
  //   generate_enemies.generate(player.position.x, point,scrollOffSet,enemies);
  //   enemies.concat(generate_enemies.enemies);

  //   enemies = [...enemies, ...generate_enemies.enemies];
  //   console.log(enemies);
  // }

  generate_enemies.generate(player.position.x, point, scrollOffSet, enemies);

  if (scrollOffSet >= 2000) {
    console.log("You win!");
  }
  if (player.position.y > canvas.height) {
    console.log("You lose!");
    init();
  }

  platform_items.platforms.forEach((platform) => {
    if (
      player.position.y + player.height <= platform.position.y &&
      player.position.y + player.height + player.velocity.y >=
        platform.position.y &&
      player.position.x + player.width >= platform.position.x &&
      player.position.x <= platform.position.x + platform.width
    ) {
      player.velocity.y = 0;
      player.isJumping = false;
      if (player.isRunning) {
        player.currentSprite = player.sprites.run.right;
      } else {
        player.currentSprite = player.sprites.stand.right;
      }
    }

    enemies.forEach((enemy) => {
      if (enemy.isActive) {
        if (
          enemy.position.y + enemy.height <= platform.position.y &&
          enemy.position.y + enemy.height + enemy.velocity.y >=
            platform.position.y &&
          enemy.position.x + enemy.width >= platform.position.x &&
          enemy.position.x <= platform.position.x + platform.width
        ) {
          enemy.velocity.y = 0;
          enemy.isJumping = false;
          if (enemy.isRunning) {
            enemy.currentSprite = enemy.sprites.run.right;
          } else {
            enemy.currentSprite = enemy.sprites.stand.right;
          }
        }
      }
    });
  });

  if (player.position.y < 0) {
    player.position.y = 0;
    player.velocity.y = 0;
  }
  enemies.forEach((enemy) => {
    if (enemy.isActive) {
      if (
        Math.abs(player.position.x - enemy.position.x) <
          enemy.width / 2 + player.width / 2 &&
        Math.abs(player.position.y - enemy.position.y) <
          enemy.height / 2 + player.height / 2
      ) {
        if (
          player.velocity.y > 0 ||
          (player.currentCharacter.name === "rhino" &&
            player.position.x + player.currentCharacter.width * 0.6 <
              enemy.position.x &&
            player.facingDirection === "right") ||
          (player.currentCharacter.name === "rhino" &&
            player.position.x - player.currentCharacter.width * 0.2 >
              enemy.position.x &&
            player.facingDirection === "left")
          // player.position.y < enemy.position.y &&
          // Math.abs(player.position.x - enemy.position.x) < enemy.width / 2
        ) {
          player.velocity.y = -player.currentCharacter.jumpHeight;
          player.isJumping = true;
          player.currentSprite = player.sprites.jump.right;

          // enemy.update = () => {};
          // enemy.position.y = -100;

          !player.playerList.includes(enemy.currentCharacter) &&
            player.playerList.push(enemy.currentCharacter);
          const index = enemies.indexOf(enemy);
          console.log(enemy.currentCharacter.name);
          Menu().add(enemy.currentCharacter.name);
          if (index > -1) {
            // only splice array when item is found
            enemies.splice(index, 1); // 2nd parameter means remove one item only
          }
        } else {
          init();
        }
      }
      enemy.update();
      enemy.attackPlayer(player);
    }
  });

  player.update();

  if (player.jump && (!player.isJumping || player.currentCharacter.canFly)) {
    player.velocity.y = -player.currentCharacter.jumpHeight;
    player.isJumping = true;
    player.currentSprite = player.sprites.jump.right;
  }
}
animate();

addEventListener("keydown", ({ keyCode }) => {
  switch (keyCode) {
    case 87:
      // Up
      player.jump = true;

      break;
    case 65:
      // Left
      keys.left.pressed = true;
      player.isRunning = true;
      player.isStanding = false;
      player.facingDirection = "left";
      player.currentSprite = player.sprites.run.left;
      break;
    case 68:
      // Right
      keys.right.pressed = true;
      player.isRunning = true;
      player.isStanding = false;
      player.facingDirection = "right";
      player.currentSprite = player.sprites.run.right;
      break;
    case 83:
      // Down
      break;
    case 32:
      keys.space.pressed = !keys.space.pressed;
      if (!player.changedCharacter) {
        player.changePlayer();
        player.changedCharacter = true;
        player.position.y -= 150;
        // player.jump = true;

        console.log(player);
        setTimeout(() => {
          // player.jump = false;
        }, 400);
      }

      setTimeout(() => {
        player.changedCharacter = false;
      }, 1000);
      break;
  }
});

addEventListener("keyup", ({ keyCode }) => {
  switch (keyCode) {
    case 87:
      // Up
      player.jump = false;
      break;
    case 65:
      // Left

      keys.left.pressed = false;

      player.isRunning = false;
      player.isStanding = true;
      player.currentSprite = player.sprites.stand.left;
      break;
    case 68:
      // Right

      keys.right.pressed = false;
      player.isRunning = false;
      player.isStanding = true;
      player.currentSprite = player.sprites.stand.right;

      break;
    case 83:
      // Down

      break;
    case 32:
      // keys.space.pressed = false;

      break;
  }
});
