import pig from "../img/AngryPig/Pig.png";
import bunny from "../img/bunny/Fall.png";

import rhino from "../img/Rino/Rhino.png";
import bat from "../img/Bat/Bat.png";
import bluebird from "../img/BlueBird/BlueBird.png";

function Menu() {
  function add(character) {
    const images = {
      pig: {
        image: (() => {
          var img = document.createElement("img");
          img.src = pig;

          return img;
        })(),
      },
      bunny: {
        image: (() => {
          var img = document.createElement("img");
          img.src = bunny;
          return img;
        })(),
      },
      rhino: {
        image: (() => {
          var img = document.createElement("img");
          img.src = rhino;
          return img;
        })(),
      },
      bat: {
        image: (() => {
          var img = document.createElement("img");
          img.src = bat;
          return img;
        })(),
      },
      bluebird: {
        image: (() => {
          var img = document.createElement("img");
          img.src = bluebird;
          return img;
        })(),
      },
    };

    var src = document.getElementById("character-list");
    try {
      src.appendChild(images[character].image);
    } catch {}
  }

  function clear() {
    var src = document.getElementById("character-list");
    src.innerHTML = "";
  }

  return {
    add,
    clear,
  };
}
export default Menu;
