import { Enemy } from "./enemy";

let enemyList = [
  "bat",
  "rabbit",
  "rhino",
  "pig",
  "bluebird",
  "snail",
  "doraemon",
  "lavabird",
  "mario",
  "michaeljackson",
];
let testlist = [
  "mario",
  "mario",
  "mario",
  "mario",
  "mario",
  "mario",
  "mario",
  "mario",
  "mario",
  "mario",
];
// enemyList = testlist;
class EnemyGenerator {
  constructor() {
    this.enemies = [];
    this.scrollPoints = [];
  }
  generate(playerPos, s_point, scrollOffSet, enemies) {
    if (
      scrollOffSet > 3500 &&
      !this.scrollPoints.includes(s_point) &&
      scrollOffSet > 0
    ) {
      if (Math.round(Math.random() * 10) <= 3) {
        enemies.unshift(
          new Enemy(
            enemyList[Math.round(Math.random() * enemyList.length - 1)],
            playerPos + 500
          )
        );
      }
    }
    this.scrollPoints.push(s_point);
  }
}

export default EnemyGenerator;
